import Vue from 'vue';
//配置路由
import VueRouter from 'vue-router'
Vue.use(VueRouter);
//1.创建组件
import Index from '@/views/index'
import Home from '@/views/home'
import Login from '@/views/login'
import NotFound from '@/views/404'
import UpdatePassword from '@/views/update-password'
import pay from '@/views/pay'
import register from '@/views/register'
import center from '@/views/center'
    import paixingleixing from '@/views/modules/paixingleixing/list'
    import news from '@/views/modules/news/list'
    import biaoqian from '@/views/modules/biaoqian/list'
    import singer from '@/views/modules/singer/list'
    import paixingbang from '@/views/modules/paixingbang/list'
    import zimu from '@/views/modules/zimu/list'
    import friendlink from '@/views/modules/friendlink/list'
    import discussgequxinxi from '@/views/modules/discussgequxinxi/list'
    import systemintro from '@/views/modules/systemintro/list'
    import yonghu from '@/views/modules/yonghu/list'
    import gequfenlei from '@/views/modules/gequfenlei/list'
    import messages from '@/views/modules/messages/list'
    import discusssinger from '@/views/modules/discusssinger/list'
    import config from '@/views/modules/config/list'
    import gequxinxi from '@/views/modules/gequxinxi/list'
    import newstype from '@/views/modules/newstype/list'


//2.配置路由   注意：名字
export const routes = [{
    path: '/',
    name: '系统首页',
    component: Index,
    children: [{
      // 这里不设置值，是把main作为默认页面
      path: '/',
      name: '系统首页',
      component: Home,
      meta: {icon:'', title:'center', affix: true}
    }, {
      path: '/updatePassword',
      name: '修改密码',
      component: UpdatePassword,
      meta: {icon:'', title:'updatePassword'}
    }, {
      path: '/pay',
      name: '支付',
      component: pay,
      meta: {icon:'', title:'pay'}
    }, {
      path: '/center',
      name: '个人信息',
      component: center,
      meta: {icon:'', title:'center'}
    }
      ,{
	path: '/paixingleixing',
        name: '排行类型',
        component: paixingleixing
      }
      ,{
	path: '/news',
        name: '音乐资讯',
        component: news
      }
      ,{
	path: '/biaoqian',
        name: '标签',
        component: biaoqian
      }
      ,{
	path: '/singer',
        name: '歌手',
        component: singer
      }
      ,{
	path: '/paixingbang',
        name: '排行榜',
        component: paixingbang
      }
      ,{
	path: '/zimu',
        name: '字母',
        component: zimu
      }
      ,{
	path: '/friendlink',
        name: '友情链接',
        component: friendlink
      }
      ,{
	path: '/discussgequxinxi',
        name: '歌曲信息评论',
        component: discussgequxinxi
      }
      ,{
	path: '/systemintro',
        name: '系统简介',
        component: systemintro
      }
      ,{
	path: '/yonghu',
        name: '用户',
        component: yonghu
      }
      ,{
	path: '/gequfenlei',
        name: '歌曲分类',
        component: gequfenlei
      }
      ,{
	path: '/messages',
        name: '意见反馈',
        component: messages
      }
      ,{
	path: '/discusssinger',
        name: '歌手',
        component: discusssinger
      }
      ,{
	path: '/config',
        name: '轮播图管理',
        component: config
      }
      ,{
	path: '/gequxinxi',
        name: '歌曲信息',
        component: gequxinxi
      }
      ,{
	path: '/newstype',
        name: '音乐资讯分类',
        component: newstype
      }
    ]
  },
  {
    path: '/login',
    name: 'login',
    component: Login,
    meta: {icon:'', title:'login'}
  },
  {
    path: '/register',
    name: 'register',
    component: register,
    meta: {icon:'', title:'register'}
  },
  {
    path: '*',
    component: NotFound
  }
]
//3.实例化VueRouter  注意：名字
const router = new VueRouter({
  mode: 'hash',
  /*hash模式改为history*/
  routes // （缩写）相当于 routes: routes
})
const originalPush = VueRouter.prototype.push
//修改原型对象中的push方法
VueRouter.prototype.push = function push(location) {
   return originalPush.call(this, location).catch(err => err)
}
export default router;
